@import url(https://fonts.googleapis.com/css?family=Darker+Grotesque:300,400,500,600,700,800,900&display=swap);
body{
    font-family: 'Darker Grotesque', sans-serif;
    font-size: 18px;
}


.logo {
    font-size: 30px;
    color: #000;
    font-weight: 800;
  }
  .mainmenu ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  .mainmenu li a {
    display: block;
    color: #000;
    text-transform: uppercase;
    font-size: 18px;
    padding: 12px;
    position: relative;
    text-decoration: none;
  }
  .mainmenu li {
    display: inline-block;
  }
  .mainmenu li a:hover::after {
    position: absolute;
    left: 50%;
    bottom: 6px;
    height: 2px;
    background: #000;
    content: "";
    width: 30px;
    margin-left: -15px;
  }
  input[type=text] {
    border: 1px solid #ddd;
    padding: 5px 15px;
  }
  input[type=submit] {
    padding: 6px 25px;
    border: none;
    color: #fff;
    background: #000;
  }
  header.header {
    padding: 20px 0;
  }
  input:focus {
    outline: none;
  } 
  .content-block {
    padding: 50px 0;
  }
  
.photo-wraper img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.photo-wraper {
  height: 190px;
  width: 250px;
}
  img {
    max-width: 100%;
  }
  h2 {
    text-align: center;
    font-weight: 800;
    margin-bottom: 30px;
  }
  .single-photo-itme h5 {
    text-transform: uppercase;
    margin-top: 20px;
    margin-bottom: 0px;
  }
  .single-photo-itme a {
    color: #333;
  }
  .cate-name {
    opacity: .5;
    font-size: 90%;
  }
  .footer-menu a {
    text-transform: uppercase;
    color: #333;
    text-decoration: none;
    padding: 15px;
    display: inline-block;
  }
  .footer-menu li {
    display: inline-block;
  }
  .footer-menu ul {
    margin: 0;
    padding: 0;
    list-style: none;
  }
  .footer-menu {
    margin-top: 30px;
    margin-bottom: 100px;
  }
  .copyright-text {
    margin-bottom: 30px;
  }
